$main-light-blue: rgba(77, 204, 233, 0.966);
$main-dark-blue: rgba(12, 205, 248, 0.966);

.profileSection {
   background-color: #fff;
   padding-top: 15px;
   height: auto;

   .jumbotron {
      font-size: 4rem;
   }

   .portrait {
      padding-top: 2rem;
   }
   img {
      margin-right: 0;
      margin-left: 0rem;
      opacity: 0.8;
      max-height: 500px;
      max-width: 70%;
      border-radius: 2%;
      border-style: solid;
   }

   h2 {
      margin-top: 1.5rem;
   }
   p {
      padding-top: 2rem;
      padding-left: 0;
      margin-left: 6rem;
      margin-right: 6rem;
      margin-bottom: 5rem;
      font-size: 28px;
      // text-shadow: black 0.3em 0.3em 0.3em;
   }
}

.button {
   background-color: $main-light-blue;
   border-color: $main-light-blue;
}

.button:hover {
   background-color: $main-dark-blue;
   border-color: $main-dark-blue;
   transform: scale(1.1) !important;
   transition: transform 0.3s ease;
}



.social {
   padding-top: 10px;
   align-items: center;
   justify-content: center;
   a {
      color: $main-dark-blue !important;
   }
   i {
      margin-left: 10px;
   }
   i:hover {
   color: $main-dark-blue;
   transform: scale(1.3) !important;
   transition: transform 0.3s ease;
}
}
@media screen and (max-width: 746px) {
   .profileSection {
      flex-wrap: inherit;

      p {
         padding-top: 1rem;
         padding-left: 0;
         margin-left: 1rem;
         margin-right: 1rem;
         margin-bottom: 3rem;
         font-size: 25px;
      }
   }
}
